import React, { Component, PropTypes } from 'react';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import * as AppActions from 'actions';
import Login from '../Login/Login';
import AppGrommet from 'grommet/components/App';
import Header from 'grommet/components/Header';
import Footer from 'grommet/components/Footer';
import Title from 'grommet/components/Title';
import Columns from 'grommet/components/Columns';


class App extends Component {
  static propTypes = {
    transactions: PropTypes.array,
    summary: PropTypes.object,
    gridFields: PropTypes.array,
    actions: PropTypes.object
  };

    

  componentWillMount() {

  }

  render() {

    return (
      <AppGrommet centered={false}>
        <Header direction="row" justify="between" size="large"
                pad={{horizontal: 'medium'}}>
          <Title>game login</Title>
        </Header>
        <Columns masonry={true}
                 size='medium'
                 justify='center'>

        <Login/>
        </Columns>
        <Footer primary={true} appCentered={true} direction="column"
                align="center" pad="small" colorIndex="grey-1">
          <p>
            Winter is coming <a href="http://#" target="_blank">Stark</a>!
          </p>
        </Footer>
      </AppGrommet>
    );
  }
}

function mapStateToProps(state) {
  const { transactions } = state;
  return {
    transactions: transactions.transactions,
    summary: transactions.summary,
    gridFields: transactions.transactionsGrid
  };
}

function mapDispatchToProps(dispatch) {
  return {
    actions: bindActionCreators(AppActions, dispatch)
  };
}

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(App);
